import {NewsTranslation} from './news-translations.model';

export const NewsStatus = {
    draft: 0, // Nhp
    pending: 1, // ang ch
    approved: 2, // ang duyt
    decline: 3 // Hy duyt
};

export class Tag {
    tagId: string;
    name: string;
}

export class News {
    id: string;
    concurrencyStamp: string;
    featureImage: string;
    bannerImage: string;
    altImage: string;
    source: string;
    isHot: boolean;
    isHomePage: boolean;
    isActive: boolean;
    categoriesNews: number[];
    modelTranslations: NewsTranslation[];

    constructor() {
        this.isHot = true;
        this.isHomePage = false;
        this.isActive = true;
        this.modelTranslations = [];
        this.categoriesNews = [];
    }
}
